import uniIcons from "../uni-icons/uni-icons.vue";
export default {
	name: "UniFav",
	components: {
		uniIcons
	},
	props: {
		star: {
			type: [Boolean, String],
			default: true
		},
		bgColor: {
			type: String,
			default: "#eeeeee"
		},
		fgColor: {
			type: String,
			default: "#666666"
		},
		bgColorChecked: {
			type: String,
			default: "#007aff"
		},
		fgColorChecked: {
			type: String,
			default: "#FFFFFF"
		},
		circle: {
			type: [Boolean, String],
			default: false
		},
		checked: {
			type: Boolean,
			default: false
		},
		contentText: {
			type: Object,
			default () {
				return {
					contentDefault: "收藏",
					contentFav: "已收藏"
				};
			}
		}
	},
	watch: {
		checked() {
			if (uni.report) {
				if (this.checked) {
					uni.report("收藏", "收藏");
				} else {
					uni.report("取消收藏", "取消收藏");
				}
			}
		}
	},
	methods: {
		onClick() {
			this.$emit("click");
		}
	}
};
